@import '/src/styles/variables.scss';
@import '/src/styles/mixins.scss';

.contact {
  @extend %container;
}

.contact__title {
  @extend %title;
}

.contact__text_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin: 0 auto;
  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    gap: 24px;
    margin-bottom: 24px;
    width: 397px;
  }

  @media screen and (min-width: 1280px) {
    gap: 40px;
    margin-bottom: 40px;
    width: 581px;
  }
}

.contact__icon {
  width: 36px;
  height: 36px;
}

.contact__text {
  @include text(400, 16px, 19px);
  text-align: center;
  text-transform: uppercase;

  @media screen and (min-width: 1280px) {
    @include text(400, 24px, 29px);
  }
}
